body {
  font-family: 'Phosphate', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f9;
}

.app {
  text-align: center;
  padding: 20px;
}

header {
  background-color: #13cd7c;
  padding: 20px;
  color: white;
}

h1 {
  margin: 0;
}

main {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.input-section, .summary-section {
  margin-bottom: 20px;
}

input {
  display: block;
  width: calc(100% - 20px);
  margin: 10px auto;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

button {
  background-color: #13cd7c;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}

button:hover {
  background-color: #29f29b;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin: 10px 0;
  padding: 10px;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 4px;
}

footer {
  margin-top: 20px;
  font-size: 14px;
  color: #888;
}